import React from 'react'
import PropTypes from 'prop-types'
import "./Career.css";

class Career extends React.Component {
  constructor(props){
    super(props);
    this.organisation = props.organisation;
    this.start = props.start;
    this.end = props.end;
    this.position = props.position;
    this.responsibility = props.responsibility;
    this.mark = props.mark;
    this.skill = props.skill;
    this.state = {
      visible: true
    }
  }

  getMonth = (d1, d2)=>{
    let month;
    month = (d2.getFullYear() - d1.getFullYear())*12;
    month -= d1.getMonth() + 1;
    month += d2.getMonth();
    return month <=0 ? 0 : month;
  }

  getTable = ()=>{
    return(
    <table className="info">
      <colgroup>
        <col className='col1'></col>
        <col className='col2'></col>
      </colgroup>
      <tbody>
        <tr>
          <td>Поступил: </td>
          <td>{this.start.toLocaleDateString()}</td>
        </tr>
        <tr>
          <td>Должность: </td>
          <td>{this.position}</td>
        </tr>
        <tr>
          <td>Обязанности: </td>
          <td><p className="text">{this.responsibility}</p></td>
        </tr>
        <tr>
          <td>Уволился: </td>
          <td>{this.end.toLocaleDateString()}</td>
        </tr>
        <tr>
          <td>Прокачал скилы: </td>
          <td><p className="text">{this.skill}</p></td>
        </tr>
        <tr>
          <td>Что понял? </td>
          <td><p className="text">{this.mark}</p></td>
        </tr>
      </tbody>
    </table>)
  }

  openClose = ()=>{
    this.setState({visible: !this.state.visible});
  }

  arrowPoint = ()=>{
    return this.state.visible ? <span>&#9650;</span> : <span>&#9660;...</span>
  }
  render () {

      return(
        <div className="Career">
          <div onClick={this.openClose} style={{"cursor":"pointer", "display":"flex"}}>
            <h4 style={
                  {
                    "margin":"0px 10px"
                  }
                }>{this.organisation}</h4>
              {this.arrowPoint()}
          </div>
          {this.state.visible ? this.getTable() : null}
        </div>
      )

  }
}

export default Career;
