import React from 'react'
import PropTypes from 'prop-types'
import "./Education.css"
import Career from "./Career.js"

class Education extends Career {
  constructor(props){
    super(props);
    this.place = props.place;
    this.start = props.start;
    this.end = props.end;
    this.education = props.education;
    this.position = props.position;
    this.cvalification = props.cvalification;
  }

  getTable = ()=>{
    return(
      <table className="info">
        <colgroup>
          <col className='col1'></col>
          <col className='col2'></col>
        </colgroup>
        <tbody>
          <tr>
            <td>Образование: </td>
            <td>{this.education}</td>
          </tr>
          <tr>
            <td>Поступил: </td>
            <td>{this.start}</td>
          </tr>
          <tr>
            <td>Окончил: </td>
            <td>{this.end}</td>
          </tr>
          <tr>
            <td>Специальность: </td>
            <td>{this.position}</td>
          </tr>

          <tr>
            <td>Квалификация: </td>
            <td>{this.cvalification}</td>
          </tr>
        </tbody>
      </table>
    )
  }

  render(){
    let element = (
      <h4>{this.place}</h4>
    )
    return(
      <div className="Career">
        <div onClick={this.openClose} style={{"cursor":"pointer", "display":"flex"}}>
          <h4 style={
                {
                  "margin":"0px 10px"
                }
              }>{this.place }</h4>
            {this.arrowPoint()}
        </div>
        {this.state.visible ? this.getTable() : null}
      </div>
    )
  }

}

export default Education;
