import React from 'react'
import PropTypes from 'prop-types'
import './ElementOfHead.css'

class ElementOfHead extends React.Component {
  constructor(props){
    super(props);
    this.name = props.name;
    this.id = props.id;
    this.changePage = props.changePage
    
  }

  changeClass = ()=>{
    this.changePage(this.id);

  }

  render () {
    return(
      <div className="ElementOfHead" onClick={this.changeClass}>
        <text class="headText">{this.name}</text>
      </div>
    )
  }
}

export default ElementOfHead;
