import React, { Component } from 'react';
import './App.css';
import Header from "./components/Header.js";
import Biography from "./pages/Biography.js";
import Projects from "./pages/Projects.js";


class App extends Component {
  constructor(){
    super();
    this.pages = {
      biography: <Biography />,
      projects: <Projects />
  };
    this.state = {
      activePage: this.pages.biography,
    }
  }

  changePage = (id)=>{

    this.setState({
      activePage: this.pages[id]
    })
  }

  render() {
    return (
      <div className="App">
        <Header changePage={this.changePage.bind(this)} activePage={this.state.activePage}/>

        {this.state.activePage}
      </div>
    );
  }
}

export default App;
